html {
  font-size: 10px;
}

*{
  box-sizing: border-box;
}

body{
  margin:0;
  padding:0;
  background-color: rgb(244,244,244);
  color:#333;

}
p{
  font-size: 1.5rem;
}
small{
  font-size: 1.6rem;

}
.container{
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}
.title{
  font-size: 4.4rem;
}
.form{
  display: grid;
}
.label{
  font-size: 1.6rem;
  padding: .5rem 2rem;
  line-height: 2.5rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: .5rem;

}
.button{
  background-color: rgba(0, 0, 0, 0.75);
  color: white;
  padding: 1rem 2rem;
  border: 1px solid rgba(0, 0, 0, 0.75) ;
  border-radius: 20px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color 250ms;

}
.button :hover{
  background-color: rgba(0, 0, 0, 0.85);
}
@media (min-width:786px){
  .form{
    grid-template-columns: auto 1fr auto;
    align-items: center;
    grid-gap: 1rem;
  }
  .input{
    margin-bottom: 0;
  }
}